  @theme {
    --color-primary: #e5e5e0;
    --color-DarkLava: #393632;
    --color-SageGray: #8b8b73;
    --color-gold: #cfa355;
    --font-amiamie: "Amiamie", sans-serif;
    --font-amiamie-round: "Amiamie-Round", sans-serif;
  
    --animate-marquee: marquee 40s infinite linear;
    @keyframes marquee {
      from {
        transform: translateX(0);
      }
      to {
        transform: translateX(calc(-100% - var(--gap)));
      }
    }
  }
  
  @keyframes orbit {
    0% {
      transform: translate(-50%, -50%) translateX(50px) translateY(0px) rotate(0);
    }
    40% {
      transform: translate(-50%, -50%) translateX(100px) translateY(-30px) rotate(30deg);
    }
    50% {
      /* Pause at B */
      transform: translate(-50%, -50%) translateX(100px) translateY(-30px) rotate(60deg);
    }
    100% {
      transform: translate(-50%, -50%) translateX(50px) translateY(0px) rotate(0);
    }
  }

  @keyframes orbit_reverse {
    0% {
      transform: rotate(0deg) translateX(50px);
    }
    25% {
      transform: rotate(22.5deg) translateX(50px);
    }
    50% {
      transform: rotate(35deg) translateX(50px);
    }
    75% {
      transform: rotate(22.5deg) translateX(50px);
    }
    100% {
      transform: rotate(0deg) translateX(50px);
    }
  }


/* ------------------------------- GENERAL ------------------------------- */

  body {
    background: #e5e5e0;
    color: black;
    scroll-behavior: smooth;
    font-family: var(--font-amiamie);
  }
  
  .clip-path {
    clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%);
  }

  .bg-gold {
    background-color: #d3a802;
  }

  .text-gold {
    color: #d3a802;
  }

  .headerImgHolder {
    width: 100%;
    height: 85vh;
    background-color: rgb(15, 12, 0);
    display: flex;
    justify-content: center;
  }

  @media (max-width: 768px) {
    .headerImgHolder {
      background-color: rgb(12, 12, 0);
    }
  }

  .text-transparent {
    color: #e5e5e0;
  }

  .overflowExperience {
    overflow-y: scroll !important;
  }

  .mainBoxShadow {
    box-shadow: 0px 0px 3px 3px #00000047;
  }

  .skillsGrid_three {
    grid-template-columns: 1fr 1fr 1fr;
    height: max-content;
    gap: 45px;
  }

  @media (max-width: 768px) {
    .skillsGrid_three {
      grid-template-columns: 1fr 1fr;
      gap: 40px;
    }
  }

  .skillsGrid_one {
    grid-template-columns: 1fr;
    height: max-content;
    gap: 30px;
  }

  .imgWorks {
    border: 3px solid #000;
  }

  @media (max-width: 1400px) {
    .aboutBody {
      flex-direction: column;
    }
  }

  .borderNavbar {
    border-left: 5px solid #d3a802;
  }

  .valuesBg {
    background: #e5e5e0;
  }

/* ------------------------------- FONTS ------------------------------- */

  .banner-text-responsive {
    font-size: 51px;
    line-height: 52px; 
  }
  
  .value-text-responsive {
    font-size: 24px; 
  }
  
  .marquee-text-responsive {
    font-size: 28px;
  }
  
  .contact-text-responsive {
    font-size: 28px;
  }


  /* fonts sm */ 
  @media (min-width: 640px) { 
    .contact-text-responsive {
        font-size: 52px;
    }
    .marquee-text-responsive {
        font-size: 36px;
    }
    .banner-text-responsive {
        font-size: 95px;
        line-height: 92px; 
    }
  }

  /* fonts md */ 
  @media (min-width: 768px) { 
    .contact-text-responsive {
      font-size: 62px;
    }
    .banner-text-responsive {
        font-size: 120px;
        line-height: 104px; 
    }
    .value-text-responsive {
        font-size: 26px;
    }
  }

  /* fonts lg */ 
  @media (min-width: 1024px) { 
    .contact-text-responsive {
        font-size: 100px;
    }
    .marquee-text-responsive {
        font-size: 42px;
    }
    .value-text-responsive {
        font-size: 32px;
    }
    .banner-text-responsive {
        font-size: 152px;
        line-height: 125px; 
    }
  }
  